* {
    font-family: Helvetica, Arial, sans-serif;
    font-size: large;
}

body {
    padding: 60px;
}

input {
    color: green;
    font-size: 1.2rem;
    width: 100%;
}

.card{
    align-content: center;
    border: 6px solid green;
    border-radius: 12px;
    margin-bottom: 10px;
    margin-right: 50px;
    overflow: hidden;
}

.card p{
    padding: 5px;
    text-align: center;
}

.card img{
    max-height: 300;
    
}

.cardgrid{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}